html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
#app {
  background: #fff;
}
body,
html {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  position: fixed;
}
.col-1 {
  float: left;
  width: 10%;
  min-height: .1rem;
}
.col-2 {
  float: left;
  width: 20%;
  min-height: .1rem;
}
.col-3 {
  float: left;
  width: 30%;
  min-height: .1rem;
}
.col-4 {
  float: left;
  width: 40%;
  min-height: .1rem;
}
.col-5 {
  float: left;
  width: 50%;
  min-height: .1rem;
}
.col-6 {
  float: left;
  width: 60%;
  min-height: .1rem;
}
.col-7 {
  float: left;
  width: 70%;
  min-height: .1rem;
}
.col-8 {
  float: left;
  width: 80%;
  min-height: .1rem;
}
.col-9 {
  float: left;
  width: 90%;
  min-height: .1rem;
}
.m-navbar.navbar-fixed {
  top: 20px!important;
}
.nav_mark {
  position: fixed;
  width: 100%;
  height: 22px;
  top: 0;
  left: 0;
  background: #fff;
  z-index: 100;
}
.back {
  display: block;
  width: .36rem;
  height: .36rem;
  background: url('assets/images/icon_back.png') center no-repeat;
  background-size: .2rem;
}
.loading {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
}
.loading > div {
  position: absolute;
  width: 2rem;
  height: 2rem;
  background: rgba(0, 0, 0, 0.2);
  border-radius: .1rem;
  top: 30%;
  left: 50%;
  margin-left: -1rem;
  text-align: center;
}
.loading > div img {
  width: 0.8rem;
  margin: 0 auto;
  margin-top: 0.6rem;
}
.vux-pop-out-enter-active,
.vux-pop-out-leave-active,
.vux-pop-in-enter-active,
.vux-pop-in-leave-active {
  will-change: transform;
  transition: all 500ms;
  backface-visibility: hidden;
  height: 100%;
  perspective: 1000;
}
.vux-pop-out-enter {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
.vux-pop-out-leave-active {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.vux-pop-in-enter {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.vux-pop-in-leave-active {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}



