
.community-box{width:100%;background: #fff;
}
.community-bg{width:100%;height:5rem;margin-top: -20px;background: url("../../assets/images/community-bg.png") center no-repeat;background-size: cover;position: relative;
}
.community-bg button{position:absolute;left:50%;bottom:0;transform:translate(-50%,50%);border: none;width: 6.5rem;height: 1.2rem;background: url("../../assets/images/community-Group@2x.png")center no-repeat;background-size: cover;margin: 0 auto;
}
.community-list{width:100%;padding: 1rem 0.3rem 0 .3rem;background: #fff;box-sizing: border-box;margin-bottom: 1rem;
}
.community-list ul{overflow: hidden
}
.community-list li{width:50%;padding-top:0.6rem;box-sizing: border-box;float:left;cursor: pointer;border-bottom: 1px solid #f4f4f4;border-right:1px solid #f4f4f4;
}
.community-list li:nth-child(2n){border-right: none;
}
.community-list li img{height:1.26rem;margin:0 auto;
}
.community-list li p{text-align: center;margin: 0.3rem 0 0.5rem;font-size: 0.35rem;width:100%;color:#969696;
}



