
.top_nav{margin-top: 1rem;
}
.resetPro{height: 12rem!important;padding-bottom: 0!important;
}
.ac-list{padding: .3rem;
}
.ac-list>li img{width: 100%;height: 2.8rem;
}
.ac-list>li{padding: .2rem 0;border-bottom: 1px solid #ddd;position: relative;
}
.ac-list>li .tag{position: absolute;top:15px;left:5px;background: rgba(0,0,0,0.8);border: 1px solid #f0c366;border-radius: 3px;color: #fff;padding: 0 5px;
}
.ac-list>li>div>p{font-size: .32rem;color: #333;
}
.s-time{font-size: .24rem;color: #999;
}
.s-time>li{overflow: hidden;
}
.s-time>li>div:last-child{text-align: right;
}
.s-time>li:first-child{border-bottom: 1px solid #eee;
}



