
.select{float: right;font-size: .28rem;margin-top: .1rem;
}
.promotiom{background: #fff;
}
.promotiom .vux-cell-box{display: none;
}
.promotiom-box{padding: 0 .3rem;
}
.promotiom-box h1{width:100%;font-size: 0.5rem;color:#4b4b4b;font-weight: normal;padding-top:.6rem;padding-bottom:.5rem;
}
.promotiom_index{background: #fff;margin-bottom: .24rem;padding: 0 .3rem;
}
.promotiom_index .swiper-slide{text-align: center;
}
.promotiom_index .swiper-slide span{font-size: .28rem;color:#afafaf;display: inline-block;padding-bottom: 0.1rem;
}
.promotiom_index .swiper-slide.active span{font-size: .28rem;color:#333;border-bottom: 1px solid #dbdbdb;display: inline-block;padding-bottom: 0.1rem;
}
.promotiom_list{width:100%;padding-bottom: 1rem;position: relative;height: 10rem;
}
.promotion-info{font-size: .28rem;color: #666;overflow: hidden;padding: .2rem 0;
}
.promotion-info .col-3{text-align: right;
}
.promotion-info .col-3 button{width: 70%;height: .45rem;line-height:.45rem;border: 1px solid #f0c366;color: #f0c366;
}
.no_data{text-align: center;color: #999;
}
.loading-layer{display: none;
}
.s-list ul{padding:0rem .3rem;margin-bottom: .5rem;
}
.s-list li{overflow: hidden;border-bottom: 1px solid #ddd;padding: .2rem 0;
}
.s-list li .s-left{float: left;width: 60%;
}
.s-list li .s-left>div{height: 2.5rem
}
.s-list li .s-left h4{font-size: .36rem;font-weight: normal;
}
.s-list li .s-left p{font-size: .28rem;color: #666;
}
.s-list li .s-left span{font-size: .28rem;color: #f0c366;
}
.s-list li .s-right{float: left;width: 40%;
}
.s-list li .s-right .s-tag{clear: both;text-align: right;padding: .2rem 0;
}
.s-list li .s-right .s-tag span{padding: 3px .2rem;background: #ddd;border-radius: 3px;color: #666;margin-top: 5px;
}
.s-list li .s-right img{width: 2.4rem;height: 2.4rem;float: right;
}



