
.un_loginmap{margin-bottom: 1rem;
}
.un_loginmap>li{border-bottom: 1px solid #eee;
}
.n_index{position: relative;z-index: 10;box-shadow: 0px 0px 6px 2px rgba(0,0,0,.3);background: #fff;transition:left .5s;left:0;min-height: 13rem;
}
.n_index .user{display: inline-block;width: .4rem;height: .5rem;background: url('../../assets/images/icon_user.png')center no-repeat;background-size: .38rem;
}
.n_index .menu{display: inline-block;width: .4rem;height: .5rem;background: url('../../assets/images/icon_menu.png')center no-repeat;background-size: .38rem;
}
.n_index .m-navbar:after{border-bottom: none!important;
}
.distance{position: absolute;top:.2rem;right: .2rem;color: #f0c366;font-size: .28rem;
}



