
.close{display: block;width: .36rem;height: .36rem;background: url("../../assets/images/icon_close_o.png")center no-repeat;background-size: .36rem;
}
.navbar-center .center-title{font-size: .36rem!important;
}
.search{position:absolute;top:0;left:0;width:100%;background: #fff;height: 100%;
}
.search .search_input{padding: .6rem .3rem;overflow: hidden;background: #f8f8f8;
}
.search .search_input input{font-size: .3rem;border: none;
}
.search .search_input .left{float: left;width: 90%;
}
.search .search_input .right{float: left;width: 10%;
}
.search .search_input .right i{float:right;width: .36rem;height: .36rem;background: url("../../assets/images/icon_search_in.png")center no-repeat;background-size: .36rem;
}
.search .c_picker{padding: .35rem 0;border-bottom: 1px solid #dddd;padding-right: .35rem;background: url("../../assets/images/icon_arrow_do.png")right center no-repeat;background-size: .34rem;border-bottom: 1px solid #eee;
}
.search .c_picker{width: 90%;
}
.search .s_picker{padding: .8rem .3rem;
}
.search .s_picker li:first-child>div{float: left;width: 50%;
}
.search .s_picker li{overflow: hidden;font-size: .28rem;color: #999;
}



