
.close{display: block;width: .36rem;height: .36rem;background: url("../../assets/images/icon_close_o.png")center no-repeat;background-size: .36rem;
}
.navbar-center .center-title{font-size: .36rem!important;
}
.top_nav{margin-top: 1rem;
}
.type-buy{padding: .3rem;
}
.type-buy>li{overflow: hidden;padding: .2rem 0;border-bottom: 1px solid #ddd;
}
.type-buy img{width: 2.4rem;height: 2.4rem;border: 1px solid #eee;
}
.type-buy>li h4{font-size: .32rem;font-weight: normal;
}
.type-buy>li p{font-size: .28rem;color: #999;height: 1.3rem;overflow: hidden;
}
.s-price>li:first-child{font-size: .32rem;color: #f0c366;
}
.s-price>li:last-child{text-align: right;
}
.s-price>li:last-child button{width: 1.6rem;height: .6rem;border-radius: .6rem;border: 1px solid #f0c366;color: #f0c366;background: #fff;
}



