
.iframe-box{position: absolute;top:0;left:0;width: 100%;height: 100%;background: #fff;
}
.iframe-box iframe{width: 100%;height: 99.5%;padding-top:1.3rem;
}
.pdf-video{position: fixed;width: 100%;height: 1rem;line-height:1rem;background: #f0c366;border-right: 1px solid  #fff;bottom: 0;left:0;
}
.pdf-video>li{float: left;text-align: center;color: #fff;font-size: .3rem;
}
.pdf-video>li:first-child{border-right: 1px solid #e6a200;
}
.one>li{width: 100%;border-right: none!important;
}
.two>li{width: 50%;
}



